.splash-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000; /* Background color */
    color: #fff; /* Text color */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 9999; /* Ensure it appears above other content */
  }
  
  .splash-logo {
    /* width: 100vh; */
    position: absolute;
    height: 130vh;
    width: auto;
    margin-bottom: 20px;
    animation: slideLogo 1s ease-in-out 1000ms forwards
    /* zoomLogo 2s 3000ms ease-in-out forwards */
    ;
    /* animation-delay: 1000ms; */
    ;
    z-index: 10;
  }
  .splash-gif {
    /* width: 100vh; */
    /* position: absolute; */
    margin-top: 200px;
    height: 100vh;
    width: auto;
    margin-bottom: 20px;
    animation: 2s fadeIn;
    animation-fill-mode: forwards;
    
    visibility: hidden;
    /* animation: slideLogo 1s ease-in-out forwards; */
    /* animation-delay: 1000ms; */
  }  
  .splash-gif2 {
    /* width: 100vh; */
    /* position: absolute; */
    /* margin-top: 200px; */
    width: 100%;
    /* height: 100vh; */
    /* width: auto; */
    margin-bottom: 20px;
    animation: 2s fadeIn;
    animation-fill-mode: forwards;
    
    visibility: hidden;
    /* animation: slideLogo 1s ease-in-out forwards; */
    /* animation-delay: 1000ms; */
  }  
  @keyframes fadeIn {
    99% {
      visibility: hidden;
    }
    100% {
      visibility: visible;
    }
  }


