.apply_page{
  height: fit-content;
  background-color:#fbfbfb;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0;
  padding-top: 15vh;
  padding-left: 30px;
  padding-right: 30px;
  display: flex;
  padding-bottom: 30px;
  width: 100vw !important;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../../images/heroBg03.png");
}
.job_containerP{
  display: flex;
  flex-direction: column;
  display: flex;
  justify-content: center;
  padding-left: 10vw;
  padding-right: 10vw;
  width: fill;
  /* align-items: center; */
}
.apply_page h1 {
  color: var(--text1)!important;
  font-size: 32px !important;
  margin-bottom: 0px;

}
.apply_page p {
padding-top: 0px;
  font-size: 16px;
  color: var(--text1)!important;
  margin-bottom: 20px;
  /* background-color: #EEF1F4; */
}
.job_box {
  background: rgba( 217, 213, 213, 0.2 );
  box-shadow: 0 8px 32px 0 rgba(37, 144, 156, 0.37);
  backdrop-filter: blur( 3px );
  -webkit-backdrop-filter: blur( 3px );
  border-radius: 10px;
  /* border: 0.5px solid rgb(255, 255, 255); */
  display: flex;
  flex-direction: column;
  width: 100% !important;
  padding: 30px;
  justify-content: space-between;
  cursor: pointer;
  z-index: 10;
  margin-bottom: 20px;
  animation: slideTop 1s!important;
}
.job_box:hover {
    border: 0.5px solid rgb(255, 255, 255);

  box-shadow: 0 8px 32px 0 rgba(37, 144, 156, 0.57);
  backdrop-filter: blur( 5px );
  -webkit-backdrop-filter: blur( 5px );
}
.job_boxUP{
  display: flex;
  flex-direction: row;
  width: 100% !important;

  justify-content: space-between;
 height: fit-content;
}
.icon_Job{
  width: 70px;
  height: 70px;
  margin-right: 20px;
  background-color: rgba( 217, 213, 213, 0.2 );
  display: flex;
  padding: 10px;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
}
.job_box h2{
  color: var(--text1)!important;
  font-size: 20px;
  font-family: mont;
  font-weight: 400;
  margin-bottom: 0px;
  padding-top: 0px;
}
.job_box p{
  font-size: 16px;
  color:  rgb(217, 213, 213) !important;
  margin-bottom: 0px;
  padding-top: 0px;
}
.job_typeBox{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  padding-top: 5px;
  gap: 10px;
}
.job_type{
  color: var(--text1)!important;
  font-size: 12px;
  font-family: mont;
  font-weight: 400;
  width: fit-content;
  padding: 0px 10px;
  border-radius: 5px;
background-color: rgba(201, 201, 197, 0.2);
}
.job_end{
  display: flex;
flex-direction: column;
align-items: flex-end;
/* background-color: tomato; */
justify-content: space-between;

}
.job_end p{
  font-size: 14px;
  color: rgb(217, 213, 213) !important;
  width: fit-content;
  /* background-color: red; */
  text-align: right !important;
  padding: 0px 0px;
}
.job_details{
  color: var(--text1)!important;
  font-size: 12px;
  font-family: mont;
  font-weight: 400;
  width: fit-content;
  padding: 0px 20px;
  border-radius: 5px;
background-color: rgba(119, 194, 219, 0.938);
}
.job_details:hover {
  background-color: var(--primary);
  color: white !important;
}
.job_Apply{ 
  color: var(--text1)!important;
  font-size: 16px;
  font-family: mont;
  font-weight: 400;
  width: fit-content;
  padding: 10px 20px;
  border-radius: 5px;
  align-self: flex-end;
  justify-self: flex-end;
  backdrop-filter: blur(10px);
  background-image: linear-gradient(to right, rgba(252, 252, 252, 0) , var(--primary));
  color: var(--white) !important ;
margin-top: 15px;
}
.job_Apply:hover {
  background-color: var(--primary);
  color: white !important;
}
