.welcome-section{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  left:0;
  background: #000;
  /* background-image: url("./images/star05.jpeg"); */
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: 100% ;
}
.content-wrap{
  position: absolute;
  top:60%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fly-in-text{
  list-style: none;
}

.fly-in-text li {
  display: inline-block;
  margin-right: 20px;
  font-size: 8em;
  color: #fff;
  opacity: 1;
  font-family: mont;
  transition: all 3s ease;
}

.fly-in-text li:nth-child(6) {
  margin-right: 0;
}
.content-hidden .fly-in-text li{
  opacity: 0;
}
.content-hidden .fly-in-text li:nth-child(1) {
 transform: translate3d(-60px,0,0);
 /* color: red !important; 
 background-color: red; */
}
.content-hidden .fly-in-text li:nth-child(2) {
  transform: translate3d(-30px,0,0);
 }
 .content-hidden .fly-in-text li:nth-child(3) {
  transform: translate3d(-10px,0,0);
 }
 .content-hidden .fly-in-text li:nth-child(4) {
  transform: translate3d(10px,0,0);
 }
 .content-hidden .fly-in-text li:nth-child(5) {
  transform: translate3d(30px,0,0);
 }
 .content-hidden .fly-in-text li:nth-child(6) {
  transform: translate3d(60px,0,0);
 }
